<template id="">
  <div id="app">
    <div class="input">
      <input type="text" name="code" v-model="code">
      <button type="button" name="button" @click="show">show</button>
      <p style="margin-top:50px">isSafari: {{isSafari}}</p>
    </div>
    <a class="testBtn" href="itms-apps://itunes.apple.com/app/id544673497" @click="open(appInfo)">
      打开app
    </a>
    <openApp @saveToCloud="saveToCloud" @goApp="goApp"/>
  </div>
</template>

<script type="text/javascript">
import openApp from './components/openApp.vue'
import { _openApp } from './utils/app'
// import {_app} from './utils/my'
import { isWeiXins } from './utils/my'
import CallApp from 'callapp-lib';
  export default {
    data() {
      return {
        code: '',
        input: 'iframe123-origin',
        appInfo: '',
        iosUrl: `mcloud://outLinks?outlink=`
      }
    },
    components: {
      openApp
    },
    computed: {
      isSafari: function() {
        return /Safari/.test(navigator.userAgent) && !/Chrome/.test(navigator.userAgent)
      }
    },
    computed: {
      // input: function() {
      //   return this.code.replace(/\s+/g,"")
      // }
    },
    mounted() {
      var goAppInfo = {
        link: `http://caiyun.feixin.10086.cn/dl/185CjzcOjFTwe`,
        pwd: 'UlndMd',
        category: '00019700101000000001/1811hjJdT00Z05620191017152733yik',
        account: '19802021498',
        name: 'picture'
      }
      let jsonInfo = JSON.stringify(goAppInfo)
      let base64Info = window.btoa(jsonInfo)
      this.appInfo = base64Info
    },
    methods: {
      sleep(interval) {
        return new Promise(resolve => {
          setTimeout(resolve, interval);
        })
      },
      async test(param) {
        console.log('open app1');
        await this.sleep(2500)
        location.href = `http://fuguiguan.cn?outlink=${param}`
        setTimeout(() => {
          console.log('end app1');
        },3000)
      },
       open(param) {
         let ua = navigator.userAgent
         let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
           isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
           if(isiOS) {

           }else {
             alert('not ios')
           }
      },
      show() {
        this.input = this.code.replace(/\s+/g,"")
      },
      saveToCloud() {
        alert('saveToCloud-(去掉toLower函数)')
        console.log('cloud--log');
      },
       isQQ(){
        return navigator.userAgent.toLowerCase().includes("qqbrowser")
      },
      async _app(param) {
        let ua = navigator.userAgent
        let isAndroid = ua.indexOf('Android') > -1 || ua.indexOf('Adr') > -1,
          isiOS = !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/)
          if(isWeiXins()) {
            alert("DEMO，请在移动端的浏览器查看！")
            return
          } else {
            console.log('start test');
            if(isiOS) {
              this.input = 'start-open-app'
              location.href = `mcloud://outLinks?outlink=${param}`
              await this.sleep(1000) // 休眠2.5s
              setTimeout(() => {
                if(!(document.hidden || document.webkitHidden)) {
                  // 唤起失败
                  location.href='itms-apps://itunes.apple.com/app/id544673497'
                }
              },2500)
            } else if(isAndroid) {
              location.href = `hecaiyun://launch//outlinks?outlink=${param}`
              await this.sleep(1000) // 休眠2.5s
              if(!(document.hidden || document.webkitHidden)) {
                // 唤起失败
                location.href='http://caiyun.feixin.10086.cn:7070/portal/client_new.jsp?v=mCloud_800'
              }
            }
          }
      },
      goApp() {
        var goAppInfo = {
          link: `http://caiyun.feixin.10086.cn/dl/185CjzcOjFTwe`,
          pwd: 'UlndMd',
          category: '00019700101000000001/1811hjJdT00Z05620191017152733yik',
          account: '19802021498',
          name: 'picture'
        }
        let jsonInfo = JSON.stringify(goAppInfo)
        let base64Info = window.btoa(jsonInfo)
        this.appInfo = base64Info

        // const options = {
        //   scheme: {
        //     protocol: 'mcloud://'
        //   },
        //   fallback: 'itms-apps://itunes.apple.com/app/id544673497'
        // }
        // const callLib = new CallApp(options)
        // callLib.open({
        //   path: 'outLinks',
        //   param: {
        //     outlink: base64Info
        //   },
        //   callback: function() {
        //     location.href = 'itms-apps://itunes.apple.com/app/id544673497'
        //   }
        // })
        // this._app(base64Info)
        _openApp(base64Info)
        console.log(`app base64info ${base64Info}`);
        console.log(`goAppInfo ${atob(base64Info)}`);

      }
    }
  }
</script>
<style>
  .testBtn {
    width: 100px;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    border: 1px solid blue;
    border-radius: 10px;
    text-align: center;
    background-color: green;
    color: #fff;
    box-shadow: 0 6px 12px rgba(94,136,155,0.8);
    text-decoration: none;
  }
</style>



